import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { 
  ArrowLeft,
  Edit,
  Save,
  X
} from 'lucide-react';

interface Candidate {
  id: number;
  full_name: string;
  name?: string;
  telegram_username?: string;
  status: string;
  results?: string;
  last_action_date: string;
  last_action_type?: string;
  created_at: string;
  updated_at: string;
}



interface Comment {
  id: number;
  hr_comment: string;
  created_at: string;
}

const CandidateDetail: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const [candidate, setCandidate] = useState<Candidate | null>(null);
  const [editData, setEditData] = useState<Partial<Candidate>>({});
  const [comments, setComments] = useState<Comment[]>([]);
  const [loading, setLoading] = useState(true);
  const [editing, setEditing] = useState(false);
  const [newComment, setNewComment] = useState('');
  const [actionMessage, setActionMessage] = useState<string | null>(null);

  useEffect(() => {
    if (id) {
      fetchCandidateData();
    }
  }, [id]);

  useEffect(() => {
    if (editing && candidate) {
      setEditData({ ...candidate });
    }
  }, [editing, candidate]);

  const fetchCandidateData = async () => {
    try {
      const [candidateRes, commentsRes] = await Promise.all([
        fetch(`/api/candidates/${id}`),
        fetch(`/api/candidates/${id}/comments`)
      ]);

      const candidateData = await candidateRes.json();
      const commentsData = await commentsRes.json();

      setCandidate(candidateData);
      setComments(commentsData);
    } catch (error) {
      console.error('   :', error);
    } finally {
      setLoading(false);
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case '': return 'status-waiting';
      case '': return 'status-passed';
      case '': return 'status-invited';
      case '': return 'status-rejected';
      default: return 'status-waiting';
    }
  };



  const handleAddComment = async () => {
    if (!candidate || !newComment.trim()) return;
    try {
      const response = await fetch(`/api/candidates/${candidate.id}/comments`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          candidate_id: candidate.id,
          hr_comment: newComment,
        }),
      });
      if (response.ok) {
        setNewComment('');
        fetchCandidateData();
      }
    } catch (error) {
      console.error('  :', error);
    }
  };

  const copyCandidateData = () => {
    if (!candidate) return;
    const data = `: ${candidate.full_name}\nTelegram: ${candidate.telegram_username || ''}\n: ${candidate.status}`;
    navigator.clipboard.writeText(data);
    setActionMessage('    ');
    setTimeout(() => setActionMessage(null), 3000);
  };

  const handleEditChange = (field: keyof Candidate, value: string) => {
    setEditData(prev => ({ ...prev, [field]: value }));
  };

  const handleSave = async () => {
    if (!candidate) return;
    try {
      const response = await fetch(`/api/candidates/${candidate.id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(editData),
      });
      if (response.ok) {
        setEditing(false);
        setActionMessage(' ');
        fetchCandidateData();
        setTimeout(() => setActionMessage(null), 3000);
      } else {
        setActionMessage(' ');
        setTimeout(() => setActionMessage(null), 3000);
      }
    } catch (error) {
      setActionMessage(' ');
      setTimeout(() => setActionMessage(null), 3000);
    }
  };



  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="spinner"></div>
      </div>
    );
  }

  if (!candidate) {
    return (
      <div className="text-center py-12">
        <h2 className="text-xl font-semibold text-gray-900">  </h2>
        <Link to="/candidates" className="text-main hover:text-secondary">
            
        </Link>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Action Message */}
      {actionMessage && (
        <div className="fixed top-4 right-4 bg-main text-white px-6 py-3 rounded-lg shadow-lg z-50">
          {actionMessage}
        </div>
      )}

      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-4">
          <Link
            to="/candidates"
            className="text-background-2 hover:text-background"
          >
            <ArrowLeft className="h-6 w-6" />
          </Link>
          <div>
            <h1 className="text-headers text-background">{candidate.full_name}</h1>
            <p className="text-main text-background-2"> </p>
          </div>
        </div>
        <div className="flex items-center space-x-3">
          <button
            onClick={() => setEditing(!editing)}
            className="btn btn-secondary flex items-center space-x-2"
          >
            {editing ? <X className="h-4 w-4" /> : <Edit className="h-4 w-4" />}
            <span>{editing ? '' : ''}</span>
          </button>
          {editing && (
            <button onClick={handleSave} className="btn btn-primary flex items-center space-x-2">
              <Save className="h-4 w-4" />
              <span></span>
            </button>
          )}
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/*   */}
        <div className="lg:col-span-2 space-y-6">
          {/*    */}
          <div className="card">
            <h2 className="text-subheaders text-background font-bold mb-4"> </h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-main text-background mb-1">
                  
                </label>
                <input
                  type="text"
                  value={editing ? editData.full_name || '' : candidate.full_name}
                  disabled={!editing}
                  onChange={e => handleEditChange('full_name', e.target.value)}
                  className="input-field"
                />
              </div>
              <div>
                <label className="block text-main text-background mb-1">
                  
                </label>
                <select
                  value={editing ? editData.status || '' : candidate.status}
                  disabled={!editing}
                  onChange={e => handleEditChange('status', e.target.value)}
                  className="input-field"
                >
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              <div>
                <label className="block text-main text-background mb-1">
                  Telegram
                </label>
                <input
                  type="text"
                  value={editing ? editData.telegram_username || '' : candidate.telegram_username || ''}
                  disabled={!editing}
                  onChange={e => handleEditChange('telegram_username', e.target.value)}
                  className="input-field"
                  placeholder="@username"
                />
              </div>

            </div>
          </div>

          {/*   */}
          <div className="card">
            <h2 className="text-lg font-semibold text-gray-900 mb-4"> ON-</h2>
            {candidate.results ? (
              <div className="space-y-4">
                <div className="border border-gray-200 rounded-lg p-4">
                  <div className="flex items-center justify-between mb-2">
                    <h3 className="font-medium text-gray-900"> </h3>
                  </div>
                  <p className="text-gray-700 whitespace-pre-wrap">{candidate.results}</p>
                </div>
              </div>
            ) : (
              <p className="text-gray-500">  </p>
            )}
          </div>
        </div>

        {/*   */}
        <div className="space-y-6">


          {/*  HR */}
          <div className="card">
            <h2 className="text-lg font-semibold text-gray-900 mb-4"> HR</h2>
            
            {/*   */}
            <div className="mb-4">
              <textarea
                value={newComment}
                onChange={(e) => setNewComment(e.target.value)}
                placeholder=" ..."
                className="input-field h-20 resize-none"
              />
              <button
                onClick={handleAddComment}
                disabled={!newComment.trim()}
                className="mt-2 btn-primary w-full"
              >
                
              </button>
            </div>

            {/*   */}
            <div className="space-y-3">
              {comments.map((comment) => (
                <div key={comment.id} className="bg-gray-50 rounded-lg p-3">
                  <p className="text-sm text-gray-700">{comment.hr_comment}</p>
                  <p className="text-xs text-gray-500 mt-1">
                    {new Date(comment.created_at).toLocaleDateString()}
                  </p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CandidateDetail; 